var Hero = function(worldDimensions) {
  this.speed = 4
  this.worldDimensions = worldDimensions
  this.setPosition()
}

Hero.prototype = {
  moveUp: function(){
      this.y -= this.speed
  },
  moveDown: function(){
    this.y += this.speed
  },
  moveLeft: function(){
    this.x -= this.speed
  },
  moveRight: function(){
    this.x += this.speed
  },
  setPosition: function(){
    this.x = this.worldDimensions.width / 2
    this.y = this.worldDimensions.height / 2
  }
}

module.exports = Hero


